*, *::before, *::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  font-family: var(--FF);
  background: var(--Dark-BG);
  color: var(--Dark-Text2);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  margin-bottom: 0.1875rem;
  font-size: 1.5rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --LimeGreen: hsl(163, 72%, 41%);
  --BrightRed: hsl(356, 69%, 56%);
  --Facebook: hsl(208, 92%, 53%);
  --Twitter: hsl(203, 89%, 53%);
  --Instagram: linear gradient hsl(37, 97%, 70%) to hsl(329, 70%, 58%);
  --YouTube: hsl(348, 97%, 39%);
  --Toggle: linear gradient hsl(210, 78%, 56%) to hsl(146, 68%, 55%);
  --Toggle-BGC: hsl(230, 22%, 74%);
  --Dark-BG: hsl(230, 17%, 14%);
  --Dark-Top-BG: hsl(232, 19%, 15%);
  --Dark-Card-BG: hsl(228, 28%, 20%);
  --Dark-Text1: hsl(228, 34%, 66%);
  --Dark-Text2: hsl(0, 0%, 100%);
  --Light-BG: hsl(0, 0%, 100%);
  --Light-Top-BG: hsl(225, 100%, 98%);
  --Light-Card-BG: hsl(227, 47%, 96%);
  --Light-Text1: hsl(228, 12%, 44%);
  --Light-Text2: hsl(230, 17%, 14%);
}

:root {
  --FF: "Inter", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.75rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
  }
}
.toggle {
  border: none;
}
.toggle__button {
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
  background-color: var(--Toggle-BGC);
}

.card {
  padding: 1.25rem;
}